body {
  margin: 0;
}

.product-cards {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.product-cards__photo {
  background-image: url("imac.202c52ff.jpeg");
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product-cards__title {
  color: #060b35;
  width: 166px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  text-decoration: none;
}

.product-cards__code {
  color: #616070;
  align-self: self-start;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
  text-decoration: none;
}

.product-cards__botton {
  box-sizing: border-box;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.product-cards__botton:hover {
  box-sizing: border-box;
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.range {
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  margin-top: 16px;
  display: flex;
}

.range__reviews {
  color: #060b35;
  align-items: flex-end;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.stars {
  display: flex;
}

.star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.price {
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  margin-top: 24px;
  display: flex;
}

.price__titel {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.price__amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}
